.loader{
    position: relative;
    background-color: rgb(235,235,235);
    overflow: hidden;
    margin-bottom: 4px;
}

.loader::after{
    display: block;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    transform: translateX(-100%);
    background: linear-gradient(90deg,transparent, rgba(255,255,255,0.4),transparent);
    animation: loading 1s infinite;
}

@keyframes loading{
    100%{
        transform: translateX(100%);
    }
}